.breadcrumbs {
  @include flex-v-center;
  flex-wrap: wrap;
  margin-bottom: 50px;


  &__item:not(:last-child) {

    &::after {
      content: '|';
      margin: 0 $xs-offset;
    }
  }

  &__link {
    position: relative;
    z-index: 2;
    font-weight: 500;
    font-size: 14px;
    color: $dark-color;

    &:not([href]) {
      color: rgba($dark-color, 0.5);
      cursor: default;
    }

    &[href]::before {
      content: '';
      position: absolute;
      top: -4px;
      left: -7px;
      z-index: -1;
      // border: 1.5px solid $accent-color;
      width: calc(100% + 14px);
      height: calc(100% + 8px);
      background-color: rgba($dark-color, 0.8);
      opacity: 0;
      visibility: hidden;
      transition: opacity .3s ease-in-out, visibility .3s ease-in-out, background-color .3s ease-in-out;
    }

    &:focus {
      outline: none;
    }

    @include hover {
      color: $light-color;
      &[href]::before {
        opacity: 1;
        visibility: visible;
      }
      &:not([href]) {
        color: rgba($dark-color, 0.5);
        cursor: default;
      }
    }

    &:active {
      color: $dark-color;

      &[href]::before {
        opacity: 1;
        visibility: visible;
        background-color: $accent-color;
      }
    }
  }
}
.breadcrumbs__link.focus-visible {
  color: $light-color;
  &::before {
    opacity: 1;
    visibility: visible;
  }
}
