.btn {
  position: relative;
  padding: $md-offset $xl-offset;
  border: 2px solid $light-color;
  border: 2px solid $accent-color;
  font-weight: 600;
  font-size: 20px;
  letter-spacing: 0.04em;
  text-transform: uppercase;
  color: $accent-color;
  background-color: transparent;
  transition: color .3s ease-in-out, background-color .3s ease-in-out, border-color .3s ease-in-out, transform .3s ease-in-out;

  @include mobile {
    padding: $s-offset $md-offset;
    font-size: 14px;
  }
  &:focus {
    outline: none;
  }

  @include hover {
    color: $dark-color;
    background-color: $accent-color;
    border-color: transparent;
  }

  &:active {
    color: $dark-color;
    background-color: darken($accent-color, 8%);
    transform: scale(0.95);
  }
}
.hero__btn.focus-visible {
  color: $dark-color;
  background-color: $accent-color;
  border-color: rgba($accent-color, 0.8);
}
