.slider-container {
  position: absolute;
  top: 0;
  left: 0;
  padding: 0;
  width: 100%;
  height: 100%;
  max-width: 100%;
  min-height: 697px;

  @include mobile {
    min-height: 550px;
  }
}
.slider {
  width: 100%;
  height: 100%;

  &__bg {
    position: relative;
    @include bg-cover;

    &:before {
      content: '';
      @include absolute-full;
      background-image: linear-gradient(rgba(0, 0, 0, 0.6));
    }
    &--slabs {
      @include image-set("../img/hero/bg-slabs");

    }
    &--fittings {
      @include image-set("../img/hero/bg-fittings");
    }
    &--materials {
      @include image-set("../img/hero/bg-materials");
    }
  }

  &__pagination {
    position: absolute;
    z-index: 5;
    top: 45%;
    right: 4%;

    @include mini-mobile {
      top: 45%;
      right: 0;
    }

    .swiper-pagination-bullet {
      display: block;
      border-radius: 2px;
      width: 10px;
      height: 20px;
      opacity: 0.6;
      background: $light-color;
      transition: background-color 0.3s ease-in-out, opacity 0.3s ease-in-out, height 0.3s ease-in-out;

      // &:focus {
      //   outline: none;
      // }
      @include mini-mobile {
        width: 5px;
      }
    }

    .swiper-pagination-bullet-active {
      display: block;
      border-radius: 2px;
      width: 10px;
      height: 32px;
      opacity: 1;
      background-color: $accent-color;
      transition: background-color 0.3s ease-in-out, opacity 0.3s ease-in-out, height 0.3s ease-in-out;

      @include mini-mobile {
        width: 5px;
      }
    }

    &.swiper-pagination-bullets .swiper-pagination-bullet {
      margin: 10px 0;
    }
  }
}
.swiper-horizontal > .swiper-pagination-bullets, .swiper-pagination-bullets.swiper-pagination-horizontal, .swiper-pagination-custom, .swiper-pagination-fraction {
  left: auto;
  bottom: 0;
  width: 11px;
}

.swiper-pagination-bullet.focus-visible {
  background-color: $light-color;
}
