.link {
  position: relative;
  z-index: 2;
  display: block;
  text-decoration: none;
  color: inherit;

  &::before {
    content: '';
    position: absolute;
    top: -4px;
    left: -7px;
    z-index: -1;
    border: 1.5px solid $accent-color;
    width: calc(100% + 14px);
    height: calc(100% + 8px);
    background-color: transparent;
    opacity: 0;
    visibility: hidden;
    transition: opacity .3s ease-in-out, visibility .3s ease-in-out, background-color .3s ease-in-out, transform .3s ease-in-out;
  }

  &:focus {
    outline: none;
  }

  @include hover {
    color: $accent-color;
    // &::before {
    //   opacity: 1;
    //   visibility: visible;
    // }
  }

  &:active {
    // color: $dark-color;
    color: $accent-color;
    color: darken($accent-color, 8%);
    transform: scale(0.95);


    // &::before{
    //   opacity: 1;
    //   visibility: visible;
    //   background-color: $accent-color;
    // }
  }
}
.link.focus-visible {
  color: $accent-color;
  // &::before {
  //   opacity: 1;
  //   visibility: visible;
  // }
}
