.benefits {
  column-gap: 8px;
  padding: 16px 0;
  min-height: 275px;

  @include high-desktop {
    gap: 16px 8px;
  }
  @include mobile {
    padding: 0;
  }

  &__item {
    position: relative;
    grid-column: 4 span;
    padding: 16px;
    color: #555;
    background: linear-gradient(#dbdae1, #a3aaba);
    box-shadow:
      5px 5px 5px rgba(0,0,0,0.1),
      15px 15px 15px rgba(0,0,0,0.1),
      20px 20px 20px rgba(0,0,0,0.1),
      30px 30px 30px rgba(0,0,0,0.1),
      inset 1px 1px 2px #fff;

    @include high-desktop {
      grid-column: 6 span;
    }
    @include small-tablet {
      grid-column: 12 span;
      padding: 16px 30px;
    }
    @include mobile {
      grid-column: 4 span;
      padding: 16px 8px;
    }

    &::before {
      content: '';
      position: absolute;
      top: 0;
      left: 0;
      width: 10px;
      height: 100%;
      background: linear-gradient(#fff, #fff, #e3e3e3);
      filter: blur(1px);
      z-index: 1;

      @include mobile {
        width: 8px;
      }
    }

    &::after {
      content: '';
      position: absolute;
      top: 0;
      right: -1px;
      width: 10px;
      height: 100%;
      background: #9d9d9d;
      filter: blur(1px);
      z-index: 1;

      @include mobile {
        width: 8px;
      }
    }
  }

  &__card {
    padding: 80px 0 20px;
    text-align: center;
    background-repeat: no-repeat;
    background-size: 57px 57px;
    background-position: top 8px center;

    @include mobile {
      padding: 60px 0 0;
      background-size: 37px 37px;
    }

    &--icon-1 {
      background-image: url(../img/about/benefits-1.svg);
    }
    &--icon-2 {
      background-image: url(../img/about/benefits-2.svg);
    }
    &--icon-3 {
      background-image: url(../img/about/benefits-3.svg);
    }
  }

  &__title {
    margin: 0;
    margin-bottom: $s-offset;
    font-weight: 700;
    font-size: $second-size;
    text-transform: uppercase;
    color: $text-dark-color;

    @include mobile {
      margin-bottom: $xs-offset;
      font-size: 16px;
    }
  }

  &__text {
    margin: 0;
    font-weight: 400;
    font-size: $second-size;
    line-height: 140%;
    text-align: center;
    color: $text-dark-color;

    @include mobile {
      font-size: 16px;
    }
  }
}
