.hero {
  position: relative;

  &__content {
    @include flex-h-center;

    position: relative;
    flex-direction: column;
    align-items: start;
    z-index: 3;
    padding: 190px 0 95px;
    max-width: 80%;

    @include high-desktop {
      max-width: 95%;
    }
    @include mobile {
      padding: 150px 0 50px;
    }
    @include mini-mobile {
      padding: 120px 0 50px;
      max-width: 100%;
    }
  }
  &__title {
    margin: 0;
    margin-bottom: 40px;
    text-align: left;
    text-transform: uppercase;
    color: $light-color;

    @include small-tablet {
      margin-bottom: $lg-offset;
    }
    @include mini-mobile {
      margin-bottom: 20px;
    }

    span {
      display: inline-block;
      margin-right: $lg-offset;
      margin-bottom: 8px;
      padding: $xs-offset $s-offset;
      border-left: 8px solid $accent-color;
      font-weight: 500;
      font-size: 20px;
      line-height: 130%;
      text-transform: uppercase;
      color: inherit;

      @include small-tablet {
        margin-right: $md-offset;
        padding: $xs-offset;
      }
      @include mobile {
        padding: 4px $xs-offset;
        font-size: 16px;
      }
      @include mini-mobile {
        padding: 2px 4px;
        border-left-width: 3px;
        font-size: 14px;
      }

      &:last-of-type {
        display: block;
        margin: 0;
        padding: 0;
        border: none;
        font-weight: 800;
        font-size: 60px;
        line-height: 130%;
        text-transform: uppercase;
        color: inherit;

        @include high-desktop {
          font-size: 50px;
        }
        @include small-tablet {
          font-size: 40px;
        }
        @include mobile {
          font-size: 30px;
        }
        @include mini-mobile {
          font-size: 20px;
        }
      }
    }
  }

  &__text {
    @include flex-v-center;

    gap: 20px;
    margin: 0;
    margin-bottom: 48px;
    padding: 30px;
    border-radius: 2px;
    width: max-content;
    font-weight: 500;
    font-size: 24px;
    color: $light-color;
    backdrop-filter: blur(25px);

    @include high-desktop {
      padding: 20px;
      font-weight: 400;
      font-size: 20px;
    }
    @include tablet {
      flex-direction: column;
      align-items: start;
    }

    @include mobile {
      margin-bottom: 35px;
      font-size: 16px;
    }
    @include mini-mobile {
      padding: 15px;
    }

    span {
      display: inline-block;
      margin: 0;
      max-width: 330px;

      @include high-desktop {
        max-width: 275px;
      }
      @include tablet {
        flex-direction: column;
        align-items: start;
        max-width: none;
      }
      @include mobile {
        max-width: 250px;
      }
    }
  }
  &__btn {
    @include mini-mobile {
      width: 100%;
    }
  }
}
