.burger {
  @include burger;
  position: relative;
  z-index: 1000;
  display: none;
  align-items: center;
  justify-content: center;
  // border: 1px solid $accent-color;
  width: 44px;
  height: 40px;
  background-color: transparent;
  cursor: pointer;

  @include tablet {
    display: inline-flex;
  }
  @include mobile {
    width: 30px;
    height: 20px;
  }
  &::before,
  &::after,
  &__line {
    top: auto;
    left: auto;
    border-radius: 2px;
    width: 30px;
    height: 2px;
    background-color: $light-color;
  }
  &__line {
    margin-bottom: -2px;
  }
  &::before {
    transform: translateY(-8px);
  }
  &::after{
    transform: translateY(8px);
  }
  &--active {
    top: 50%;

    &::before {
      transform: rotate(45deg);
    }
    &::after {
      transform: rotate(-45deg);
    }
  }
}

.burger-close {
  position: absolute;
  display: none;
  top: 34px;
  right: $container-offset;
  // border: 1px solid $accent-color;
  width: 44px;
  height: 44px;
  background-image: url(../img/close.svg);
  background-position: center;
  background-size: 24px 24px;
  background-repeat: no-repeat;

  @include tablet {
    display: block;
  }
}
