.products {
  padding: $section-offset 0;
  background-color: $dark-color;

  &__title {
    color: $light-color;
  }

  &__list {
    @include mobile {
      grid-template-columns: repeat(4, 1fr);
    }
  }

  &__item {
    grid-column: 3 span;

    @include high-desktop {
      grid-column: 4 span;
    }
    @include small-tablet {
      grid-column: 6 span;
    }
    @include mobile {
      grid-column: 4 span;
    }
  }
}
