.product {
  @include flex-all-center;

  flex-direction: column;
  margin: 0 auto;
  color: $text-dark-color;
  background-color: $light-color;

  &__container {
    padding-top: 35px;
  }
}
.product-content {
  margin-bottom: 70px;

  @include mobile {
    margin-bottom: 50px;
  }

  &__img {
    grid-column: 5 span;
    display: flex;
    align-items: start;
    padding-top: 20px;
    max-width: 530px;

    @include tablet {
      grid-column: 12 span;
      align-items: center;
      justify-content: center;
      padding-top: 0px;
      max-width: 100%;
    }

    img {
      display: block;
      width: 90%;
      max-height: 300px;
      object-fit: contain;
    }
  }
  &__descr {
    grid-column: 7 span;
    display: flex;
    flex-direction: column;
    align-items: start;
    justify-content: space-between;

    @include tablet {
      grid-column: 12 span;
    }

    p {
      margin: 0;
      margin-bottom: $xs-offset;
      font-weight: 400;
      font-size: 20px;
      line-height: 140%;
      color: inherit;

      @include mobile {
        font-size: 16px;
      }

      &:last-of-type {
        margin-bottom: 30px;
      }
    }
  }
  &__btn {
    color: $dark-color;
    background-color: rgba($accent-color, 0.8);

    @include tablet {
      font-size: 18px;
    }
    @include mobile {
      font-size: 14px;
    }
    @include mini-mobile {
      width: 100%;
    }

    @include hover {
      color: $light-color;
      background-color: rgba($dark-color, 0.8);
      border-color: rgba($dark-color, 0.8);
    }
  }
}
