.products-card {
  position: relative;
  height: 100%;
  transform: scale(1.0);
  transition: transform .3s ease-in-out;

  &:focus {
    outline: none;
  }

  @include hover {
    transform: scale(1.04);
  }

  &:active {
    transform: scale(0.95);
  }

  &__title {
    display: block;
    margin: 0;
    font-weight: 400;
    font-size: $second-size;
    text-decoration: none;
    color: $light-color;

    @include mini-mobile {
      font-size: 16px;
    }

    &::before {
      content: "";
      position: absolute;
      inset: 0;
    }

    &:focus {
      outline: none;
    }

    @include hover {
      color: $accent-color;

      &::before{
        outline: 1px solid $accent-color;
        outline-offset: 7px;
      }
    }

    &:active {
      color: $accent-color;

      &::before{
        outline: 1px solid $accent-color;
        outline-offset: 7px;
      }
    }
  }

  &__img-container {
    @include flex-all-center;
    position: relative;
    margin-bottom: 13px;
    padding: 25px;
    min-height: 308px;
    background-color: rgba(#4b4c4f, 0.4);

    @include mobile {
      padding: 15px;
      min-height: 250px;
    }

    ::before {
      content:'';
      // position: absolute;
      // top: 0;
      // left: 0;
      z-index: 2;
      width: 100px;
      height: 100%;
      // background-color: rgba(#4b4c4f, 0.4);
      background-color: red;
      // transform: scale(45deg);
    }
  }

}

