.footer {
  padding: $section-offset 0 60px;
  background-color: $dark-color;

  &__container {
    align-items: end;

    @include small-tablet {
      grid-column: 12 span;
      grid-template-rows: repeat(3, auto);
      justify-content: center;
    }
  }

  &__policy {
    grid-column: 9 / 4 span;
    justify-self: end;
    font-style: normal;
    font-weight: 400;
    font-size: 12px;
    line-height: 125%;
    letter-spacing: 0.03em;
    text-align: center;
    color: $light-color;

    @include small-tablet {
      grid-column: 12 span;
      grid-row: 2/ 1 span;
      justify-self: initial;
    }
  }

  &__copy {
    grid-column: 4 span;
    justify-self: start;
    margin-bottom: 7px;
    padding-left: 20px;
    font-weight: 400;
    font-size: 12px;
    line-height: 125%;
    letter-spacing: 0.03em;
    text-align: center;
    color: $light-color;
    background-repeat: no-repeat;
    background-size: 14px 14px;
    background-position: 0 0;
    background-image: url(../img/copy.svg);

    @include small-tablet {
      grid-column: 12 span;
      grid-row: 3/ 1 span;
      justify-self: center;
    }
  }

  &__content {
    grid-column: 5 / 4 span;

    @include flex-all-center;
    flex-direction: column;
    gap: 24px;

    @include small-tablet {
      grid-column: 12 span;
      grid-row: 1 span;
      justify-self: initial;
    }
  }

  &__contacts {
    @include flex-v-center;
    flex-direction: column;
  }

  &__link {
    font-weight: 400;
    font-style: normal;
    font-size: 16px;
    color: $light-color;

    &:not(:last-child) {
      margin-bottom: $xs-offset;
    }
  }
}

