.nav {

  @include tablet {
    position: fixed;
    top: 0;
    right: 0;
    z-index: 300;
    overflow-y: auto;
    max-width: 400px;
    width: 100%;
    height: 100%;
    backdrop-filter: blur(25px);
    opacity: 0;
    visibility: hidden;
    overflow-y: auto;
    transform: translateX(100%);
    transition: opacity .3s ease-in-out, visibility .3s ease-in-out, transform .3s ease-in-out;

    .site-container--page-product & {
      background-color: $dark-color;
    }
  }
  @include mobile {
    max-width: none;
  }

  &__list {
    @include flex-v-center;

    @include tablet {
      flex-direction: column;
      align-items: start;
      padding: 124px 30px 50px;
    }
  }

  &__item {
    width: max-content;

    @include tablet {
      width: 100%;
    }

    &:not(:last-child) {
      margin-right: $xl-offset;

      @include high-desktop {
        margin-right: 24px;
      }
      @include tablet {
        margin-right: 0;
      }
    }
  }

  &__link {
    display: inline-block;
    padding: 12px 0;
    font-weight: 600;
    font-size: $main-size;
    color: inherit;
    transition: color .3s ease-in-out;

    @include tablet {
      display: block;
      padding: 18px 0;
      font-size: 18px;
    }

    &--submenu {
      margin-right: 16px;

      @include tablet {
        padding-right: 20px;
        margin-right: 0;
      }

      &::before {
        width: calc(100% + 14px + 16px);

        @include tablet {
          width: 100%;
        }
      }
      &::after {
        content: '';
        position: absolute;
        top: 50%;
        right: -16px;
        width: 8px;
        height: 8px;
        border-bottom: 2px solid $light-color;
        border-right: 2px solid $light-color;
        transform: translateY(-60%) rotate(45deg);
        transition: transform .3s ease-in-out;

        @include tablet {
          right: 0;
          width: 10px;
          height: 10px;
          transform: translateY(-90%) rotate(45deg);
        }

        .nav__item:hover > & {
          border-color: currentColor;
        }
      }
    }
  }

}

.nav.menu--active {
  transform: none;
  opacity: 1;
  visibility: visible;
}
.nav__item:focus-within > .submenu {
  opacity: 1;
  visibility: visible;
}
@include tablet {
  .nav__item--submenu.active > .submenu {
    display: block;
  }
  .nav__item--submenu.active > .nav__link--submenu::after {
    transform: rotate(225deg);
  }
}
