.graph-modal__close {
  top: 25px;
  right: 25px;
  @include mobile {
    top: 15px;
    right: 15px;
  }
}
.popup {
  padding: 60px 50px 50px;
  max-width: 600px;
  color: $dark-color;
  background-color: $light-color;

  @include mobile {
    padding: 40px 20px 20px;
    max-width: 290px;
  }

  &__descr {
    margin: 0;
    margin-bottom: 16px;
    max-width: 500px;
    font-weight: 600;
    font-size: 20px;
    line-height: 150%;
    text-align: center;
    color: inherit;

    @include mobile {
      font-size: 16px;
    }
  }

  &__contacts {
    @include flex-all-center;
    flex-direction: column;
    font-style: normal;
    font-weight: 400;
    font-size: 20px;
    color: inherit;
  }

  &__subtitle {
    margin: 0;
    margin-bottom: 35px;
    font-style: normal;
    font-weight: 400;
    font-size: 20px;
    text-align: center;
    color: inherit;

    @include mobile {
      margin-bottom: 24px;
      font-size: 16px;
    }

    span {
      display: inline-block;
      margin-bottom: 16px;

      @include mobile {
        margin-bottom: 10px;
      }
    }
  }

  &__link {
    font-style: normal;
    font-weight: 400;
    font-size: 20px;
    text-align: center;
    color: inherit;

    @include mobile {
      font-size: 16px;
    }

    &:not(:last-child) {
      margin-bottom: 8px;
    }
  }
  &__social {
    @include mobile {
      a {
        width: 50px;
        height: 50px;
      }
      svg {
        width: 28px;
        height: 28px;
      }
    }
  }
}
