.social {
  @include flex-all-center;

  &__item {
    &:not(:last-child) {
      margin-right: 20px;
    }
    a {
      @include flex-all-center;
      border: 1px solid rgba($accent-color, 0.8);
      border-radius: 100%;
      width: 40px;
      height: 40px;
      color: $dark-color;
      background-color: rgba($accent-color, 0.8);
      transform: scale(1.0);
      transition: transform .3s ease-in-out, outline .3s ease-in-out, background-color .3s ease-in-out, color .3s ease-in-out;

      &:focus {
        outline: none;
      }
//вариант-1
      // @include hover {
      //   outline: 1px solid $accent-color;
      //   outline-offset: 5px;
      // }
//вариант-2
      // @include hover {
      //   transform: scale(1.1);
      //   }
//вариант-2
      @include hover {
        border-color: rgba($light-color, 0.8);
        background-color: rgba($light-color, 0.8);
        }
      &:active {
        color: $accent-color;
        border-color: $accent-color;
        background-color: $dark-color;
      }
    }
  }

  &__icon {
    width: 23px;
    height: 23px;
    fill: currentColor;
  }
}
