// base
$font-family: "Montserrat", sans-serif;
$content-width: 1380px;
$container-offset: 30px;
$container-offset-mobile: 20px;
$container-width: calc( $content-width + ($container-offset * 2));

// colors
$light-color: #fff;
$dark-color: #27292e;
$accent-color: #fff000;
$text-dark-color: #000;

// size
$main-size: 16px;
$second-size: 20px;

//offset
$section-offset: 50px;
$xs-offset: 8px;
$s-offset: 16px;
$md-offset: 20px;
$lg-offset: 30px;
$xl-offset: 35px;
