.delivery-card {
  @include flex-v-center;

  flex-direction: column;
  height: 100%;
  color: $dark-color;


  &__title {
    position: relative;
    counter-increment: item;
    margin: 0;
    margin-bottom: 16px;
    padding: 6px 0 0px 60px;
    font-weight: 700;
    font-size: 20px;
    text-transform: uppercase;
    color: inherit;

    @include mobile {
      margin-bottom: 8px;
      padding: 3px 0 0px 50px;
      font-size: 16px;
    }

    &::before {
      content: "0"counter(item);
      position: absolute;
      z-index: 2;
      top: 4px;
      left: 22px;
      font-weight: 700;
      font-size: 24px;
      line-height: 106%;
      text-align: center;
      color: rgba(0, 0, 0, 0.6);

      @include mobile {
        font-size: 16px;
      }
    }

    &::after {
      content: '';
      position: absolute;
      top: 0;
      left: 8px;
      width: 32px;
      height: 32px;
      background-image: url(../img/square.svg);
      background-repeat: no-repeat;

      @include mobile {
        width: 20px;
        height: 20px;
        background-size: 20px 20px;
      }
    }
  }

  &__text {
    margin: 0;
    font-weight: 400;
    font-size: $second-size;
    line-height: 150%;
    text-align: center;
    color: inherit;

    @include mobile {
      font-size: 16px;
      line-height: 140%;
    }
  }
}
