  &__table {
    width: 100%;
    margin: 0;
    padding: 30px;
    border-collapse: collapse;
    border-spacing: 0;
    box-shadow:
      5px 5px 5px rgba(0,0,0,0.1),
      15px 15px 15px rgba(0,0,0,0.1),
      20px 20px 20px rgba(0,0,0,0.1),
      30px 30px 30px rgba(0,0,0,0.1),
      inset 1px 1px 2px #fff;

    @include small-tablet {
      border: 0;
    }
    thead {
      background: linear-gradient(#dbdae1, #a3aaba);
      tr {
        color: #555;
        background: linear-gradient(#dbdae1, #a3aaba);
      }

      &::before {
        background: linear-gradient(#fff, #fff, #e3e3e3);
      }


      @include small-tablet {
        display: none;
      }
    }
    tbody {
      @include small-tablet {
        display: flex;
        flex-wrap: wrap;
      }

      tr {
        margin-bottom: 2px;
        background-color: #fff;

        @include small-tablet {
          display: block;

          &:not(:last-child) {
            margin-bottom: 10px;
          }
        }
      }


    }

    tr {
      position: relative;
      border: 1px solid #ddd;

      @include small-tablet {
        margin-bottom: 10px;
        display: block;
        width: 100%;
        padding: 16px;
        background: linear-gradient(#dbdae1, #a3aaba);
        box-shadow:
          5px 5px 5px rgba(0,0,0,0.1),
          15px 15px 15px rgba(0,0,0,0.1),
          20px 20px 20px rgba(0,0,0,0.1),
          30px 30px 30px rgba(0,0,0,0.1),
          inset 1px 1px 2px #fff;
      }
      @include mobile {
        padding: 10px;
      }

      &::before {
        content: '';
        position: absolute;
        top: 0;
        left: 0;
        width: 10px;
        height: 100%;
        background: linear-gradient(#fff, #e3e3e3, #e3e3e3);
        filter: blur(1px);
        z-index: 1;

        @include small-tablet {
          background: linear-gradient(#fff, #fff, #e3e3e3);
        }
      }

      &::after {
        content: '';
        position: absolute;
        top: 0;
        right: -1px;
        width: 10px;
        height: 100%;
        background: #9d9d9d;
        filter: blur(1px);
        z-index: 1;

        @include small-tablet {
          background: #9d9d9d;
        }
      }
    }

    th, td {
      padding: 20px 12px;
      text-align: start;

      @include mobile {
        padding: 15px 10px;
      }
      @include mini-mobile {
        padding: 15px 6px;
      }

      &:first-child {
        padding-left: 20px;

        @include small-tablet {
          padding-left: 12px;
        }
        @include mini-mobile {
          padding-left: 6px;
        }
      }
      &:last-child {
        padding-right: 20px;

        @include small-tablet {
          padding-right: 12px;
        }
        @include mini-mobile {
          padding-right: 6px;
        }
      }
    }

    th {
      font-weight: 500;
      font-size: 20px;
      color: $dark-color;
    }


    td {
      font-weight: 400;
      font-size: 18px;

      @include small-tablet {
        display: block;
        text-align: right;
        // font-size: 13px;
        border-bottom: 1px dotted #ccc;
        border-right: 1px solid transparent;

        &:last-child {
          border-bottom: 0;
        }
        &:first-child {
          text-transform: uppercase;
          font-weight: 700;
          font-size: 20px;
          text-align: center;
        }
        &:before {
          content: attr(data-label);
          float: left;
          text-transform: uppercase;
          font-weight: 700;
        }
      }
      @include mobile {
        font-size: 16px;

        &:first-child {
          font-size: 18px;
        }
      }
      @include mini-mobile {
        font-size: 14px;

        &:first-child {
          font-size: 16px;
        }
      }

    }
  }



