.delivery {
  margin-top: -1px;
  padding: $section-offset 0;
  background-color: $light-color;
  color: $dark-color;

  &__title {
    color: inherit;
  }
  &__content {
    margin-bottom: $lg-offset;
  }

  &__descr {
    grid-column: 7 span;
    font-weight: 400;
    font-size: $second-size;
    line-height: 170%;
    color: inherit;

    @include tablet {
      grid-column: 12 span;
    }
    @include mobile {
      font-size: 16px;
    }

    p {
      margin: 0;

      &:not(:last-child) {
        margin-bottom: $xs-offset;
      }
    }

    ul {
      list-style: none;
      margin: 0;
      padding: 0;
    }

    li {
      padding-left: 35px;
      background-repeat: no-repeat;
      background-position: center left 10px;
      background-size: 20px 20px;
      background-image: url(../img/square.svg);
    }
  }

  &__cars {
    @include flex-all-sb;

  }

  &__list {
    counter-reset: item 0;
    gap: 8px;

    @include high-desktop {
      grid-template-rows: repeat(4, 1fr);
      grid-auto-flow: column;
      gap:  16px 8px;
    }
  }

  &__item {
    grid-column: 3 span;
    position: relative;
    padding: 35px 20px;
    color: #555;
    background: linear-gradient(#dbdae1, #a3aaba);
    box-shadow:
      5px 5px 5px rgba(0,0,0,0.1),
      15px 15px 15px rgba(0,0,0,0.1),
      20px 20px 20px rgba(0,0,0,0.1),
      30px 30px 30px rgba(0,0,0,0.1),
      inset 1px 1px 2px #fff;

    @include high-desktop {
      grid-column: 6 span;
      grid-row: 2 span;
    }
    @include small-tablet {
      grid-column: 12 span;
      grid-row: 1 span;
      padding: 35px 70px;
    }
    @include mobile {
      grid-column: 4 span;
      grid-row: 1 span;
      padding: 20px 16px;
    }

      &::before {
        content: '';
        position: absolute;
        top: 0;
        left: 0;
        width: 10px;
        height: 100%;
        background: linear-gradient(#fff, #fff, #e3e3e3);
        filter: blur(1px);
        z-index: 1;

        @include mobile {
          width: 8px;
        }
      }

      &::after {
        content: '';
        position: absolute;
        top: 0;
        right: -1px;
        width: 10px;
        height: 100%;
        background: #9d9d9d;
        filter: blur(1px);
        z-index: 1;

        @include mobile {
          width: 8px;
        }
      }
  }
}
.delivery-cars {
  @include flex;

  grid-column: 5 span;
  flex-direction: column;
  align-items: center;
  position: relative;

  @include tablet {
    grid-column: 12 span;
  }

  &__item {
    @include flex;
    flex-direction: column;
    align-items: end;
    font-weight: 400;
    font-size: $second-size;
    line-height: 170%;
    color: #969696;
    color: inherit;
    z-index: 2;

  }
}
