.header {
  position: absolute;
  top: 0;
  left: 0;
  z-index: 10;
  padding: 22px 0;
  width: 100%;
  color: $light-color;

  .site-container--page-product & {
    position: relative;
    background-color: $dark-color;
    color: $light-color;
  }

  &__container {
    @include flex-all-sb;
  }

  &__contacts {
    @include flex;
    flex-direction: column;

    @include tablet {
      display: none;
    }
  }
  &__logo {
    @include mobile {
      width: 220px;
    }
    @include mini-mobile {
      width: 173px;
      height: 41px;
    }
  }
  &__link {
    position: relative;
    z-index: 2;
    width: fit-content;
    font-weight: 600;
    font-size: $main-size;
    font-style: normal;
    line-height: 162%;
    color: inherit;
  }
}

