.about {
  position: relative;
  z-index: 2;
  padding: $section-offset 0 0;
  background-color: $light-color;

  &__title {
    color: $text-dark-color;
  }

  &__content {
    column-gap: $xl-offset;
    margin-bottom: $xl-offset;

    @include mobile {
      margin-bottom: $md-offset;
    }
  }

  &__img {
    position: relative;
    grid-column: 6 span;
    width: 100%;
    min-height: 336px;
    background-repeat: no-repeat;
    background-size: cover;
    background-position: center left 20px;
    @include image-set-png("../img/about/about");



    @include tablet {
      grid-column: 12 span;
      height: 100%;
      min-height: 400px;
    }
    @include small-tablet {
      min-height: 300px;
    }
    @include mobile {
      grid-column: 4 span;
      min-height: 250px;
    }

    &::before {
      content: '';
      position: absolute;
      z-index: -1;
      display: block;
      top: -20px;
      width: calc((60%)/2);
      height: calc(100% + 20px * 2);
      background-color: $accent-color;

      @include tablet {
        width: calc((50%)/2);
      }
    }
  }

  &__descr {
    grid-column: 6 span;

    @include tablet {
      grid-column: 12 span;
      padding-top: $xl-offset;
    }

    @include mobile {
      grid-column: 4 span;
      padding-top: $md-offset;
    }

    p {
      margin: 0;
      font-weight: 400;
      font-size: $second-size;
      line-height: 140%;
      color: $text-dark-color;

      @include mobile {
        font-size: 16px;
      }
    }

    p:not(:last-child) {
      margin-bottom: $s-offset;
    }
  }
}
